/*

 */

import React, { Component } from "react";
import { NavBar, WingBlank, List, InputItem, WhiteSpace, Button } from "antd-mobile";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";

import { login } from "../../redux/actions";

import Logo from "../../components/logo/logo";

// const ListItem = List.Item;

class Login extends Component {
  state = {
    username: "", // 
    password: "", // 
  };

  login = () => {
    this.props.login(this.state);
  };

  // : 
  handleChange = (name, val) => {
    // 
    this.setState({
      [name]: val, // name, name
    });
  };

  toRegister = () => {
    this.props.history.replace("/register");
  };

  render() {
    const { redirectTo } = this.props.user;
    // const { msg, redirectTo } = this.props.user;
    // redirectTo, 
    if (redirectTo) {
      return <Redirect to={redirectTo} />;
    }

    return (
      <div>
        <NavBar>TALENTS&nbsp;&nbsp;&nbsp;HUB</NavBar>
        {/* <NavBar>&nbsp;&nbsp;&nbsp;</NavBar> */}
        <Logo />
        <WingBlank>
          <List>
            {/* {msg ? <div className='error-msg'>{msg}</div> : null} */}
            <WhiteSpace />
            <InputItem
              style={{ border: "1px solid gray", borderRadius: "5px", height: "40px", paddingLeft: "5px" }}
              placeholder="Username"
              // placeholder=""
              onChange={(val) => {
                this.handleChange("username", val);
              }}>
              {/* : */}
            </InputItem>
            <WhiteSpace />
            <InputItem
              style={{ border: "1px solid gray", borderRadius: "5px", height: "40px", paddingLeft: "5px" }}
              placeholder="Password"
              // placeholder=""
              type="password"
              onChange={(val) => {
                this.handleChange("password", val);
              }}>
              {/* &nbsp;&nbsp;&nbsp;: */}
            </InputItem>
            <WhiteSpace />

            <Button type="primary" style={{ fontWeight: "bold", fontSize: "24px" }} onClick={this.login}>
              Log&nbsp;&nbsp;in
            </Button>
            {/* <Button type="primary" onClick={this.login}>
              &nbsp;&nbsp;&nbsp;
            </Button> */}
            <WhiteSpace />
            <Button onClick={this.toRegister}>Register Now</Button>
            {/* <Button onClick={this.toRegister}></Button> */}
          </List>
        </WingBlank>
      </div>
    );
  }
}

export default connect((state) => ({ user: state.user }), { login })(Login);



