/*

  dashen: /dashen
  laoban: /laoban

  dashen: /dasheninfo
  laoban: /laobaninfo
? user.header
: user.type
 */
/*

 */
export function getRedirectTo(type, header) {
  let path
  // type
  if(type==='laoban') {
    path = '/laoban'
  } else {
    path = '/dashen'
  }
  // header
  if(!header) { // , path
    path += 'info'
  }

  return path
}


