/*
UI
 */

import React from "react";
import { Button } from "antd-mobile";

class NotFound extends React.Component {
  render() {
    return (
      <div>
        <div>
          <h2>sorry, page does not exist</h2>
          {/* <h2>!</h2> */}
          <Button type="primary" onClick={() => this.props.history.replace("/")}>
            Back to Home
            {/*  */}
          </Button>
        </div>
      </div>
    );
  }
}

export default NotFound;



