/*

 */

import React, { Component } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import Cookies from "js-cookie"; // cookie set()/get()/remove()
import { NavBar } from "antd-mobile";

import LaobanInfo from "../laoban-info/laoban-info";
import DashenInfo from "../dashen-info/dashen-info";
import Dashen from "../dashen/dashen";
import Laoban from "../laoban/laoban";
import Message from "../message/message";
import Personal from "../personal/personal";
import NotFound from "../../components/not-found/not-found";
import NavFooter from "../../components/nav-footer/nav-footer";
import Chat from "../chat/chat";

import { getRedirectTo } from "../../utils";
import { getUser } from "../../redux/actions";

class Main extends Component {
  // 
  navList = [
    // 
    {
      path: "/laoban", // 
      component: Laoban,
      title: "DEVELOPERS LIST",
      icon: "dashen",
      text: "Developer",
    },
    {
      path: "/dashen", // 
      component: Dashen,
      title: "BOSSES LIST",
      icon: "laoban",
      text: "Boss",
    },
    {
      path: "/message", // 
      component: Message,
      title: "MESSAGES LIST",
      icon: "message",
      text: "Message",
    },
    {
      path: "/personal", // 
      component: Personal,
      title: "PERSONAL CENTER",
      icon: "personal",
      text: "personal",
    },
  ];
  // // 
  // navList = [ // 
  //   {
  //     path: '/laoban', // 
  //     component: Laoban,
  //     title: '',
  //     icon: 'dashen',
  //     text: '',
  //   },
  //   {
  //     path: '/dashen', // 
  //     component: Dashen,
  //     title: '',
  //     icon: 'laoban',
  //     text: '',
  //   },
  //   {
  //     path: '/message', // 
  //     component: Message,
  //     title: '',
  //     icon: 'message',
  //     text: '',
  //   },
  //   {
  //     path: '/personal', // 
  //     component: Personal,
  //     title: '',
  //     icon: 'personal',
  //     text: '',
  //   }
  // ]

  componentDidMount() {
    //(cookieuserid), (reduxuser_id) user
    const userid = Cookies.get("userid");
    const { _id } = this.props.user;
    if (userid && !_id) {
      // , user
      // console.log('ajaxuser')
      this.props.getUser();
    }
  }

  render() {
    // cookieuserid
    const userid = Cookies.get("userid");
    // , 
    if (!userid) {
      return <Redirect to="/login" />;
    }
    // ,reduxuser
    const { user, unReadCount } = this.props;
    // user_id, null()
    // debugger
    if (!user._id) {
      return null;
    } else {
      // _id, 
      // , usertypeheader, 
      let path = this.props.location.pathname;
      if (path === "/") {
        // 
        path = getRedirectTo(user.type, user.header);
        return <Redirect to={path} />;
      }
    }

    const { navList } = this;
    const path = this.props.location.pathname; // 
    const currentNav = navList.find((nav) => nav.path === path); // nav, 

    if (currentNav) {
      // 
      if (user.type === "laoban") {
        // 2
        navList[1].hide = true;
      } else {
        // 1
        navList[0].hide = true;
      }
    }

    return (
      <div>
        {currentNav ? <NavBar className="sticky-header">{currentNav.title}</NavBar> : null}
        <Switch>
          {navList.map((nav) => (
            <Route key={nav.path} path={nav.path} component={nav.component} />
          ))}
          <Route path="/laobaninfo" component={LaobanInfo} />
          <Route path="/dasheninfo" component={DashenInfo} />
          <Route path="/chat/:userid" component={Chat} />

          <Route component={NotFound} />
        </Switch>
        {currentNav ? <NavFooter navList={navList} unReadCount={unReadCount} /> : null}
      </div>
    );
  }
}

export default connect((state) => ({ user: state.user, unReadCount: state.chat.unReadCount }), { getUser })(Main);

/*
1. :
  1. componentDidMount()
    (cookieuserid), (reduxuser_id) user:
  2. render()
    1). cookieuserid, login
    2). reduxuser_id, , 
    3). , , 
    4). : usertypeheader, 
 */



