/*

 */
import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { NavBar, InputItem, Button } from "antd-mobile";
// import { NavBar, InputItem, TextareaItem, Button } from "antd-mobile";
import HeaderSelector from "../../components/header-selector/header-selector";

import { updateUser } from "../../redux/actions";

class LaobanInfo extends Component {
  state = {
    header: "",
    post: "",
    info: "",
    company: "",
    salary: "",
  };

  // header
  setHeader = (header) => {
    this.setState({
      header,
    });
  };

  handleChange = (name, value) => {
    this.setState({
      [name]: value,
    });
  };

  save = () => {
    this.props.updateUser(this.state);
  };

  render() {
    // , 
    const { header, type } = this.props.user;
    if (header) {
      // 
      const path = type === "dashen" ? "/dashen" : "/laoban";
      return <Redirect to={path} />;
    }

    return (
      <div>
        <NavBar>Please Complete Boss Info</NavBar>
        {/* <NavBar></NavBar> */}
        <HeaderSelector setHeader={this.setHeader} />
        <InputItem
          placeholder="Job Intention"
          onChange={(val) => {
            this.handleChange("post", val);
          }}></InputItem>
        {/* <InputItem placeholder='' onChange={val => {this.handleChange('post', val)}}>:</InputItem> */}
        <InputItem
          placeholder="Company Name"
          onChange={(val) => {
            this.handleChange("company", val);
          }}></InputItem>
        {/* <InputItem
          style={{ border: "1px solid gray", borderRadius: "5px", height: "40px", paddingLeft: "5px" }}
          placeholder="Please enter Company Name"
          onChange={(val) => {
            this.handleChange("company", val);
          }}></InputItem> */}
        {/* <InputItem placeholder='' onChange={val => {this.handleChange('company', val)}}>:</InputItem> */}
        <InputItem
          placeholder="Job Salary"
          onChange={(val) => {
            this.handleChange("salary", val);
          }}></InputItem>
        {/* <InputItem placeholder='' onChange={val => {this.handleChange('salary', val)}}>:</InputItem> */}
        <InputItem
          placeholder="Job Requirement"
          rows={1}
          onChange={(val) => {
            this.handleChange("info", val);
          }}></InputItem>
        {/* <TextareaItem title=":"
                      placeholder=''
                      rows={3} onChange={val => {this.handleChange('info', val)}}/> */}
        <Button type="primary" style={{ marginTop: "5px" }} onClick={this.save}>
          Save
        </Button>
        {/* <Button type='primary' onClick={this.save}>&nbsp;&nbsp;&nbsp;</Button> */}
      </div>
    );
  }
}

export default connect((state) => ({ user: state.user }), { updateUser })(LaobanInfo);



