/*

 */

import React, { Component } from "react";
import { NavBar, WingBlank, List, InputItem, WhiteSpace, Radio, Button } from "antd-mobile";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";

import { register } from "../../redux/actions";
import Logo from "../../components/logo/logo";

const ListItem = List.Item;

class Register extends Component {
  state = {
    username: "", // 
    password: "", // 
    password2: "", // 
    type: "laoban", //    dashen/laoban
  };

  // 
  register = () => {
    //console.log(this.state)
    this.props.register(this.state);
  };

  // : 
  handleChange = (name, val) => {
    // 
    this.setState({
      [name]: val, // name, name
    });
  };

  toLogin = () => {
    this.props.history.replace("/login");
  };

  render() {
    const { type } = this.state;
    const { redirectTo } = this.props.user;
    // const { msg, redirectTo } = this.props.user;
    // redirectTo, 
    if (redirectTo) {
      return <Redirect to={redirectTo} />;
    }

    return (
      <div>
        <NavBar>TALENTS&nbsp;&nbsp;&nbsp;HUB</NavBar>
        {/* <NavBar>&nbsp;&nbsp;&nbsp;</NavBar> */}
        <Logo />
        <WingBlank>
          <List>
            {/*  {msg ? <div className='error-msg'>{msg}</div> : null} */}
            <WhiteSpace />
            <InputItem
              style={{ border: "1px solid gray", borderRadius: "5px", height: "40px", paddingLeft: "5px" }}
              placeholder="Username"
              // placeholder=""
              onChange={(val) => {
                this.handleChange("username", val);
              }}>
              {/* username */}
              {/* : */}
            </InputItem>
            <WhiteSpace />
            <InputItem
              style={{ border: "1px solid gray", borderRadius: "5px", height: "40px", paddingLeft: "5px" }}
              placeholder="Password"
              // placeholder=""
              type="password"
              onChange={(val) => {
                this.handleChange("password", val);
              }}>
              {/* password */}
              {/* &nbsp;&nbsp;&nbsp;: */}
            </InputItem>
            <WhiteSpace />
            <InputItem
              style={{ border: "1px solid gray", borderRadius: "5px", height: "40px", paddingLeft: "5px" }}
              placeholder="Confirm Password"
              // placeholder=""
              type="password"
              onChange={(val) => {
                this.handleChange("password2", val);
              }}>
              {/* confirm password */}
              {/* : */}
            </InputItem>
            <WhiteSpace />
            <ListItem>
              <span>User Type</span>
              {/* <span>:</span> */}
              &nbsp;&nbsp;&nbsp;
              <Radio checked={type === "dashen"} onChange={() => this.handleChange("type", "dashen")}>
                Developer
                {/*  */}
              </Radio>
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
              <Radio checked={type === "laoban"} onClick={() => this.handleChange("type", "laoban")}>
                Boss
                {/*  */}
              </Radio>
            </ListItem>
            <WhiteSpace />
            <Button type="primary" style={{ fontWeight: "bold", fontSize: "24px" }} onClick={this.register}>
              Register
              {/* &nbsp;&nbsp;&nbsp; */}
            </Button>
            <WhiteSpace />
            <Button onClick={this.toLogin}>Log in</Button>
            {/* <Button onClick={this.toLogin}></Button> */}
          </List>
        </WingBlank>
      </div>
    );
  }
}

export default connect((state) => ({ user: state.user }), { register })(Register);



